.menu {
  height: calc(100% - 60px);
  position: fixed;
  background-color: white;
  bottom: 0;
  left: 0;
  width: 100%;
  transform: translateY(105%);
  transition: .8s ease-out;
  z-index: 5;
  &.open {
    transform: translateY(0%); }
  .menu__list {
    list-style: none;
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    overflow-y: auto;
    &.visible {
      animation: show 1s 0s linear both; }
    .menu__list-listItem {
      margin: 0.5rem;
      width: 100%;
      &.visible {
        animation: showListItem 1s ease both; }
      &.visible:nth-of-type(1) {
        animation-delay: 1.5s; }
      &.visible:nth-of-type(2) {
        animation-delay: 1.75s; }
      &.visible:nth-of-type(3) {
        animation-delay: 2s; }
      &.visible:nth-of-type(4) {
        animation-delay: 2.25s; }
      &.visible:nth-of-type(5) {
        animation-delay: 2.5s; }
      a {
        display: block;
        margin: 0 auto;
        font-size: 50px;
        text-align: center;
        text-decoration: none;
        color: black;
        transition: .3s linear; } } }
  .menu__socialIcons {
    position: absolute;
    bottom: 0;
    right: 0;
    .socialIcons__icon {
      display: block;
      margin-bottom: 10px;
      margin-right: 20px;
      &.visible {
        animation: showListItem 1s ease both; }
      &.visible:nth-of-type(1) {
        animation-delay: 2.75s; }
      &.visible:nth-of-type(2) {
        animation-delay: 3s; }
      i {
        font-size: 25px;
        color: black; } } } }

@media (min-width:1024px) {
  .menu {
    display: flex;
    justify-content: space-around;
    &.open {}
    .menu__list {
      justify-content: center;
      list-style: none;
      .menu__list-listItem {
        a {
          font-size: 100px;
          position: relative;
          span {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 0%;
            height: 100%;
            background-color: rgba(0,0,0,0.9);
            z-index: -1;
            border-radius: 60px;
            transition: .5s linear; }
          &:hover {
            color: white; }
          &:hover span {
            width: 40%; } } } } } }

@keyframes show {
  from {
    opacity: 0;
    transform: translateY(-5px); }
  to {
    opacity: 1;
    transform: translateY(0px); } }
@keyframes showPicture {
  from {
    opacity: 0;
    transform: translateX(100%); }
  to {
    opacity: 1;
    transform: translateX(0%); } }

@keyframes showListItem {
  from {
    opacity: 0;
    transform: translateY(50px); }
  to {
    opacity: 1;
    transform: translateY(0px); } }
